<template>
<div id="userPage">
    <div class="demo-image" style="width:200px;height:200px;">
        <div class="block">
            <el-image :fit="fit" :src='url'></el-image>
        </div>
    </div>
    <div class="userMes">
        <el-form :model="userForm" status-icon :rules="rule" ref="userForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户名" prop="userNo" id="userNo">
                <el-input v-model="userForm.userNo" :disabled="status"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
                <el-input v-model="userForm.email" :disabled="status"></el-input>
            </el-form-item>
            <el-form-item label="电话" prop="phone">
                <el-input v-model.number="userForm.phone" :disabled="status"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="commit" :disabled="isnotOk" :loading="xiugai">提交</el-button>
                <el-button type="success" @click="modify">修改</el-button>
            </el-form-item>
        </el-form>
        <el-button  class="check" type="primary" size="small" @click="checkUserNo" :disabled="status">检测用户名重名</el-button>
    </div>
</div>
</template>
<script>
import { setTimeout } from 'timers';
export default {
    data(){
        return{
            status: true,
            isnotOk: false,
            xiugai: false,
            userForm: {
                id:"",
                userNo:"",
                email:"",
                phone:""
            },
            rule:{
                userNo:[{required:true,trigger:"blur",message:'用户名不能为空'}],
                email:[{type:"email",message:"请输入正确的邮箱地址",trigger:["blur"]}],
                phone:[{type:"number",message:"电话格式有误",trigger:"blur"}]
            },
            url:require('../../assets/head/header.jpg')
        }
    },
    watch:{
       'userForm.userNo':function(){
            if(this.userForm.userNo != sessionStorage.getItem('userNo'))
                this.isnotOk = true
            else
                this.isnotOk = false
        } 
    },
    mounted(){
        this.getUserMes()
    },
    methods:{
        getUserMes(){
            let user =sessionStorage.getItem('userNo')
            if(user != null)
                this.$store.commit('setNowUserNo',user)
            this.axios.getUserMes({'id':sessionStorage.getItem('id')}).then((res)=>{
                let data = res.dataList[0]
                this.userForm.id = data.id
                this.userForm.userNo = data.userNo
                this.userForm.email = data.email
                this.userForm.phone = Number(data.phone)
            })
        },
        checkUserNo(){
            this.axios.checkUserName({'userNo':this.userForm.userNo}).then((res)=>{
                if(res.success)
                {
                    this.isnotOk = false
                    this.$message.success('用户名可以使用')
                }
                else{
                    this.$message.error('用户名被占用')
                }
            })
        },
        modify(){
            this.status = !this.status
        },
        commit(){
            this.xiugai = true
            this.$refs["userForm"].validate((valid)=>{
                if(valid){
                    this.axios.modifyUser(this.userForm).then((res)=>{
                        if(res.success){
                            this.$message.success("更新成功")
                            setTimeout(function(){this.xiugai = false;history.go(0)},1000)
                        }
                        else{
                            this.$message.error('更新失败,请检查网络错误')
                        }
                    })
                }
                else{
                    this.$message.error("注册信息错误")
                }
            })
        }
    }
}
</script>
<style lang="scss">
#userPage{
    .userMes{
        width: 450px;
        position: relative;
        left: 600px;
        top: 85px;
        .el-button{
            width: 30%;
            margin-right: 40px;
            margin-left: 10px;
        }
    }
    .demo-image{
        position: absolute;
        top:160px;
        left: 300px;
    }
    .check{
        position: relative;
        left: 320px;
        bottom: 243px;
    }
}
</style>
